








import Vue, { ComponentOptions }  from 'vue'
//import { MyClass } from '../app.ts'
//import OtherComponent from './OtherComponent.vue';

interface MyComponent extends Vue {
  message: string
  onClick (): void
}
export default {
  components: {
    //OtherComponent
  },
  data: function () {
    return {
      message: `Hello!`
    }
  },
  methods: {
    onClick: function () {
      window.alert(this.message + " - ");
    }
  }
} as ComponentOptions<MyComponent>



